import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-checkup-t2-s1',
  templateUrl: './checkup-t2-s1.component.html',
  styleUrls: ['./checkup-t2-s1.component.css']
})
export class CheckupT2S1Component implements OnInit {
  constructor() { }

  @Output() next = new EventEmitter();

  ngOnInit() {
  }


  back(){
    this.next.emit(({
      naxtPage: 't1-s2'
    }));
  }

  save() {
    this.next.emit(({
      naxtPage: 't2-s2',
      type: 'profession',
      modal: {
      },
    }));
  }
}



