import {HMO, MaritalStatus, privateInsurance, typeOfPrivateInsurance} from "../enums/enums";
export class Health {
  "gender": string;
  "birthDate": string;
  "doesSmoke": boolean;
  "smokeInLast3Years": boolean;
  "privateName": string;
  "lastName": string;
  "prefix": string;
  "number": string;
  "email": string;
  "agreeToContract": boolean;
  "maritalStatus": MaritalStatus;
  "hmo" : HMO;
  "hmoExtra" : boolean;
  "privateInsurance": privateInsurance;
  "typeOfPrivateInsurance": typeOfPrivateInsurance;
  "children": number;
  childrens: any[];
}



