import {EventEmitter, Injectable} from '@angular/core';
import {Http, RequestOptions} from "@angular/http";

@Injectable()
export class StoreService {

  headerEvent = new EventEmitter();

  store = {}

  constructor() {
  }

  getByKey(key){
    return this.store[key]
  }
  setByKey(key, value){
    this.store[key] = value;
  }

  refresh(){
    this.store = {};
  }
}



