import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-checkup-t2-s2',
  templateUrl: './checkup-t2-s2.component.html',
  styleUrls: ['./checkup-t2-s2.component.css']
})
export class CheckupT2S2Component implements OnInit {

  constructor() { }

  @Output() next = new EventEmitter();

  ngOnInit() {
  }

  back(){
    this.next.emit(({
      naxtPage: 't2-s1'
    }));
  }
  save() {
    this.next.emit(({
      naxtPage: 't2-s3',
      type: 'profession',
      modal: {
      },
    }));
  }

}



