import { Component, OnInit } from '@angular/core';
import {NgxCarousel} from "ngx-carousel";
import {StoreService} from "../shared/service/store.service";
declare var lory;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  public carouselOne: NgxCarousel;
  public carousel: NgxCarousel;
  showTab = 'property';
  showSMS;
  showError;
  daysData = [];
  number ="";
  constructor(private storeService: StoreService){
    storeService.headerEvent.subscribe(()=> {
      this.showSMS = true;
    });
  }

  setDays(){
    for (let i = 50; i <= 300; i+=10){
      this.daysData.push( {
        label: i.toString(),
        value: i.toString()
      });
    }
  }

  sendSms(){
    if(!this.number)
      this.showError = true;
    else
      this.showSMS = false;
  }

  ngOnInit() {
    this.setDays();
    this.carouselOne = {
      grid: {xs: 1, sm: 1, md: 1, lg: 1, all: 0},
      slide: 1,
      speed: 400,
      interval: 2400,
      point: {
        visible: true
      },
      load: 2,
      touch: true,
      loop: true,
    };
    this.carousel = {
      grid: {xs: 4, sm: 4, md: 4, lg: 4, all: 0},
      slide: 1,
      speed: 400,
      interval: 4000,
      point: {
        visible: true
      },
      load: 2,
      touch: true,
      loop: true,
    };
setTimeout(() => {
  var simple = document.querySelector('.js_slider');

  lory(simple, {
    rewind: true
  });
  // setInterval(()=> {
  //   console.log(23)
  //   document.getElementById("nextStep").click();
  // },3000);
}, 6000);

    setInterval(()=> {
      console.log(23)
      if(document.getElementById("nextStep"))
      document.getElementById("nextStep").click();
    },3000);
  }

  public myfunc(event: Event) {
    // carouselLoad will trigger this funnction when your load value reaches
    // it is helps to load the data by parts to increase the performance of the app
    // must use feature to all carousel
  }

}



