import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-checkup-t2-s3',
  templateUrl: './checkup-t2-s3.component.html',
  styleUrls: ['./checkup-t2-s3.component.css']
})
export class CheckupT2S3Component implements OnInit {
  seconds = 59;
  minuts = 59;
  hours = 23;
  days = 2;
  constructor() { }

  ngOnInit() {
    setInterval(()=>{
      this.seconds--;
      if (this.seconds === 0){
        this.minuts--;
        this.seconds = 59;
        if (this.minuts === 0){
          this.minuts = 59;
          this.hours--;
          if(this.hours === 0){
            this.days--;
            this.hours = 23;
          }
        }
      }
    }, 1000);
  }
  @Output() next = new EventEmitter();



  back(){
    this.next.emit(({
      naxtPage: 't2-s2'
    }));
  }
  save() {
    // this.next.emit(({
    //   naxtPage: 't2-s3',
    //   type: 'profession',
    //   modal: {
    //   },
    // }));
  }
}



