import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {StoreService} from "../../shared/service/store.service";

@Component({
  selector: 'app-checkup-t1-s2',
  templateUrl: './checkup-t1-s2.component.html',
  styleUrls: ['./checkup-t1-s2.component.css']
})
export class CheckupT1S2Component implements OnInit {


  showError;

  obj = {
    hmo:"",
    extra:"",
    smoking: "",
    yearSmokin: "",
    email: "",
    phone: "",
    firstName: "",
    lastName: "",
    id: "",
    job: "בחר מקצוע",
    rate: "1000 - 10000"
  };
  jobsData = [
    {
      label: "בחר מקצוע",
      value: "בחר מקצוע"
    },
    {
      label: "דייל/דיילת מכירו",
      value: "דייל/דיילת מכירו"
    },
  ];

  rateData = [
    {
      label: "1000 - 10000",
      value: "1000 - 10000"
    },
    {
      label: "10000 - 20000",
      value: "10000 - 20000"
    },
  ];

  phoneData = [
    {
      label: "050",
      value: "050"
    },
    {
      label: "040",
      value: "040"
    },
    {
      label: "060",
      value: "060"
    },
    {
      label: "070",
      value: "070"
    },
  ];

  constructor(private storeService: StoreService) { }

  @Output() next = new EventEmitter();

  ngOnInit() {
    let store = this.storeService.getByKey("checkup-t1-s1");
    if(store){
      this.obj = store;
    }
  }

  jobsChange(e){
    if(e)
    this.obj.job = e;
  }

  rateChange(e){
    if(e)
      this.obj.rate = e;
  }

  save() {
    if(!this.obj.rate) {
      this.obj.rate =  "1000 - 10000";
    }
    if(!this.obj.job){
      this.obj.job = "בחר מקצוע";
    }

    if(!this.obj.job || !this.obj.rate || !this.obj.email || !this.obj.phone || !this.obj.firstName || !this.obj.lastName || !this.obj.id ||
      !this.obj.extra || !this.obj.hmo || (!this.obj.smoking && !(this.obj.smoking === false as any))){
      this.showError = true;
    } else {
      if(this.obj.smoking === false as any && !this.obj.yearSmokin){
        this.showError = true;
        return;
      }

      this.next.emit(({
        naxtPage: 't2-s1',
        type: 'checkup-t1-s2',
        modal: {
        },
      }));

      this.storeService.setByKey("checkup-t1-s1", this.obj);

    }
  }

  back(){
    this.next.emit(({
      naxtPage: 't1-s1'
    }));
  }

}



