import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {StoreService} from "../../shared/service/store.service";

@Component({
  selector: 'app-genderselect',
  templateUrl: './genderselect.component.html',
  styleUrls: ['./genderselect.component.css']
})
export class GenderselectComponent implements OnInit {
  gender;
  showError;
  constructor(private storeService: StoreService) {
    let store = this.storeService.getByKey("genderselect");
    if(store){
      this.gender = store;
    }
  }

  @Output() next = new EventEmitter();
  @Input() nextOption;
  ngOnInit() {
  }

  save() {
    if(!this.gender){
      this.showError = true;
    } else {
      this.next.emit(({
        // naxtPage: 'birthdate',
        // naxtPage: 'private-health-personalinfo',
        naxtPage: this.nextOption,
        model: this.gender,
        type: 'genderselect'
      }));
      this.storeService.setByKey("genderselect", this.gender);

    }
  }

}



