import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {StoreService} from "../../shared/service/store.service";

@Component({
  selector: 'app-health-insurance',
  templateUrl: './health-insurance.component.html',
  styleUrls: ['./health-insurance.component.css']
})
export class HealthInsuranceComponent implements OnInit {
  constructor(private storeService: StoreService) { }
  manExist = "";
  manType= "";
  wifeExist = "";
  wifeType = "";
  childExist = "";
  childType = "";
  showError;

  info = {

  };
  @Output() next = new EventEmitter();

  ngOnInit() {
    let info = this.storeService.getByKey("private-health-personalinfo");
    if(info){
      this.info = info;
    }
  }

  save() {
    if(!this.manExist || (!this.manType && this.manExist !== '2') ||
      ((this.info as any).wifeName && (!this.wifeExist || !this.wifeType) && this.wifeExist !== '2') ||
      ((this.info as any).childAmount && (!this.childExist || !this.childType) && this.childExist !== '2')
    ){
      this.showError = true;
    } else {

      this.next.emit(({
        finish: true,
        naxtPage: 'health-insurance',
        type: "health-insurance",
        model: this.manExist
      }));
    }
  }

  back(){
    this.next.emit(({
      naxtPage: 'kupot-holim'
    }));
  }
}



