import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {StoreService} from "../../shared/service/store.service";

@Component({
  selector: 'app-kupot-holim',
  templateUrl: './kupot-holim.component.html',
  styleUrls: ['./kupot-holim.component.css']
})
export class KupotHolimComponent implements OnInit {

  constructor(private storeService: StoreService) { }
  obj = {
    manHMO: "",
    manHMOextra: "",
    wifeHMO: "",
    wifeHMOextra: "",
    childHMO: "",
    childHMOextra: ""
  }
  info = {
    wifeName: 2,
    childAmount: 2,
  }
  showError;
  @Output() next = new EventEmitter();

  ngOnInit() {
    let store = this.storeService.getByKey("kupot-holim");
    if(store){
      this.obj = store;
    }

    this.info = this.storeService.getByKey("private-health-personalinfo");

  }

  save() {
    if(((!this.obj.manHMO && !(this.obj.manHMO as any === false)) || !this.obj.manHMOextra) ||
      ((this.info as any).wifeName && ((!this.obj.wifeHMO && !(this.obj.wifeHMO as any === false)) || !this.obj.wifeHMOextra)) ||
      ((this.info as any).childAmount && ((!this.obj.childHMO && !(this.obj.childHMO as any === false)) || !this.obj.childHMOextra))){
      this.showError = true;
    } else {

      this.next.emit(({
        naxtPage: 'health-insurance',
        type: "kupot-holim",
        model: this.obj
      }));

      this.storeService.setByKey("kupot-holim", this.obj);

    }
  }

  back(){
    this.next.emit(({
      naxtPage: 'private-health-personalinfo'
    }));

  }
}



