import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {StoreService} from "../../shared/service/store.service";

@Component({
  selector: 'app-checkup-t1-s1',
  templateUrl: './checkup-t1-s1.component.html',
  styleUrls: ['./checkup-t1-s1.component.css']
})
export class CheckupT1S1Component  implements OnInit {

  yearsData = [];
  monthData = [
    {
      label: "1",
      value: "1"
    },
    {
      label: "2",
      value: "2"
    },
    {
      label: "3",
      value: "3"
    },
  ];
  daysData = [];
  obj = {
    day: "1",
    month: "1",
    year: "1925",
    yearSmokin: "",
    raiodMaritalStatus: "",
    radioWifeSmoke: "",
    wifeName: "",
    wifV1: "",
    childAmount: 0,
    smoking: null,
    childrens: [],
    gender: ""
  };

  showError;
  showDrop;
  constructor(private storeService: StoreService) {
    this.setYear();
    this.setDays();
  }

  @Output() next = new EventEmitter();

  ngOnInit() {
    let store = this.storeService.getByKey("checkup-t1-s1");
    if(store){
      this.obj = store;
    }
  }

  setDays(){
    for (let i = 1; i <= 31; i++){
      this.daysData.push( {
        label: i.toString(),
        value: i.toString()
      });
    }
  }

  setYear() {
    for (let i = 1925; i <= 2018; i++){
      this.yearsData.push( {
        label: i.toString(),
        value: i.toString()
      });
    }
  }



  daysChange(e){
    this.obj.day = e;
  }

  monthChange(e){
    this.obj.month = e;
  }

  yearsChange(e){
    this.obj.year = e;
  }
  doChangeChildren(){
    this.obj.childrens = [];

    for(let i = 0; i < this.obj.childAmount; i++){
      this.obj.childrens.push({stat: "", radio: "", name: ""})
    }
  }

  save() {
    let childFlag;
    if(!this.obj.year ||
      !this.obj.month ||
      !this.obj.year
      || !this.obj.raiodMaritalStatus){
      this.showError = true;
      console.log(22)

    } else {

      for(let i = 0; i < this.obj.childAmount; i++){
        if(!this.obj.childrens[i].name || !this.obj.childrens[i].radio){
          childFlag = true;
          break;
        }
      }

      if(!this.obj.gender){
        this.showError = true;
      }

      if(childFlag){
        this.showError = true;
        return;
      }


      this.next.emit(({
        naxtPage: 't1-s2',
        date: this.obj.day + '-' + this.obj.month + '-' + this.obj.year,
        smoking: this.obj.smoking,
        raiodMaritalStatus: this.obj.raiodMaritalStatus,
        childrens: this.obj.childrens,
        smokeInLast3Years: this.obj.yearSmokin,
        type: "checkup-t1-s1"
      }));

      this.storeService.setByKey("checkup-t1-s1", this.obj);
    }
  }

}



